
// Padding
.p-1by8 { padding: 1rem 8rem; }
.p-2by8 { padding: 2rem 8rem; }
.p-3by8 { padding: 3rem 8rem; }
.p-4by8 { padding: 4rem 8rem; }
.p-5by8 { padding: 5rem 8rem; }

// Margin
.m-b-0 { margin-bottom: 0 !important; }
.m-b-1-5 { margin-bottom: 1.5rem !important; }
.m-b-2 { margin-bottom: 2rem !important; }
.m-b-3 { margin-bottom: 3rem !important; }
.m-b-4 { margin-bottom: 4rem !important; }
.m-b-5 { margin-bottom: 5rem !important; }
 // Negative margin
.m-b--1-5 { margin-bottom: -1.5rem; }

// Text
.txt-neon { color: $color-neon; }
.txt-gray { color: $color-gray; }
.txt-dark { color: $color-dark; }
.txt-red { color: $color-red; }
.txt-white { color: $color-white; }

// Background color
.bg-pink { background-color: $color-pink; }
.bg-neon { background-color: $color-neon; }
.bg-white { background-color: $color-white; }
.bg-light-2 { background-color: $color-light-2; }

// Flexbox order
.order-1 { order: 1 };
.order-2 { order: 2 };

// Text align
.txt-center { text-align: center; }

// Active
.active,
.sale__shoe-box:hover {
    background-color: $color-neon !important;
}

